/* eslint-disable no-unused-vars */
import React, { Component } from "react"
import MetaTags from 'react-meta-tags';

import { MDBDataTable } from "mdbreact"
import  { Redirect } from 'react-router-dom'

import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Spinner,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"
import conf from '../../../conf.js';
// import Select from "react-select"
// import axios from 'axios'
// import  { Redirect } from 'react-router-dom'
// // import $ from "jquery";

// import Flatpickr from "react-flatpickr"
// import ReactSelect from 'react-select'
// import makeAnimated from 'react-select/animated';
//import BootstrapSelect from "bootstrap-select"

import Confirmer from '../../../components/Confirmer.js'
class ListeTerrain extends Component {


  constructor(props) {
    super(props)
    this.state = {
      terrainData:[],
      terrains:[],
      ok:"",
      show:false,
      page:<div style={{marginTop:"25%",marginBottom:"20%",textAlign:"center"}}>
      <Spinner type="grow" color="success" />
   </div>,
      id:0,
      status:true
    }
    this.handleDetail.bind(this)
    this.supprimer.bind(this)
    this.handleModifier.bind(this)
    this.handleIndisponibilite.bind(this)
  }
  componentDidMount() {
    let  data=  JSON.parse(localStorage.getItem('token_auth'))
    const user = data.user
    const role = data.role
    const requestOptions = {
      method: 'GET',
      headers: { 
        'Authorization':'Bearer '+data.token,
  
      },
    };

    fetch(conf.url+"terrain/terrains",requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("data",data)
      this.setState({status:false})
      var terrains=data
      
      try{
        if(data.length){
          
          if(role!="ADMIN"){
            this.setState({terrainData:data.filter(el=>el.idUser==user.id)})
          }else{
            this.setState({terrainData:data})
          }
        }else{
          this.setState({terrainData:[]})
        }
       }catch(error){
        this.setState({terrainData:[]})
       }
    
        
  
      })
  }


  handleSupprimer=()=>{

    let  data=  JSON.parse(localStorage.getItem('token_auth'))
    const requestOptions = {
      method: 'GET',
      headers: { 
        'Authorization':'Bearer '+data.token,

      },
    };
    
  
  
  fetch(conf.url+"terrain/terrains/delete/"+this.state.id, requestOptions)
    
    .then(data =>{console.log("enregitre avec succes vrai:",data)
     console.log("data send",data)
      this.setState({terrains:this.state.terrains.filter(element=>element.id!=this.state.id)})
      this.setState({show:false})
     
   } )
  }




  handleDetail=(id)=>{
    localStorage.setItem('idTerrain',id);
    this.setState({ok: <Redirect to='/detail-terrain'/>});
  }
  handleIndisponibilite=(id)=>{
    localStorage.setItem('idTerrain',id);
    this.setState({ok: <Redirect to='/indisponibilite'/>});
  }
  toggled=()=>{
    this.setState({show:!this.state.show})
  }

  supprimer=(id)=>{
  
    this.setState({id:id,show:true})
    
  }

  handleModifier=(id)=>{
    localStorage.setItem('idTerrain',id);
    this.setState({ok: <Redirect to='/modifier-terrain'/>});
    
  }

  render() {

    const data = {
      columns: [
        {
          label: "Nom",
          field: "nom",
          sort: "asc",
          width: 150,
        },
        {
          label: "Adresse",
          field: "adresse",
          sort: "asc",
          width: 100,
        },
        {
          label: "Ville",
          field: "ville",
          sort: "asc",
          width: 100,
        },
     
        {
          label: "Prix/Heure",
          field: "prix_heure",
          sort: "asc",
          width: 100,
        },
        {
          label: "Type",
          field: "type",
          sort: "asc",
          width: 100,
        },
        {
          label: "Disponibilité",
          field: "disponibilite",
          sort: "asc",
          width: 100,
        },
        {
          label: "Action",
          field: "action",
          sort: "asc",
          width: 100,
        },
        // {
        //   label: "Proprietaire",
        //   field: "proprietaire",
        //   sort: "asc",
        //   width: 100,
        // },
       
      ],
      rows:this.state.terrains
      
    }


    return (
      <React.Fragment>
        <div className="page-content"> 
       
          {this.state.ok}

          <Container fluid={true}>
          {
          (!this.state.status)?

          (
            <Row>
              <Col md="12" className="pull-right">
                <Card>
                  <CardBody>
                    <Row ClassName="mb-4">
                      <Col lg="6" md="6" sm="12" className="mb-2">
                        <div className="page-title-box d-flex align-items-center justify-content-between">
                          <h3 className="mb-0 font-size-18">Liste des terrains</h3>
                        </div>
                      </Col>

                      <Col lg="6" md="6" sm="12" className="pull-right">
                        <Button
                          color="success"
                          className="font-16 btn btn-success float-right"
                          href="/#/ajout-terrain"
                        >
                          <i class='bx bx-plus-circle' > </i>  Ajouter un nouveau terrain
                        </Button>
                      </Col>
                    </Row>
                    <Confirmer show={this.state.show} handleValider={this.handleSupprimer} toggle={this.toggled}/>
                    <hr/>

                    <Row ClassName="mt-5">
                  {  this.state.terrainData.map((element,idx)=>(
                    <Col lg="6" md="6" sm="12" className="mt-2">
                    <Card className="block" id="block" style={{borderRadius:"25px",borderColor:"#4f8f3f",borderWidth:"5px",backgroundColor:"#FFFFF"}}>
                      <CardBody>
                        <CardTitle className="h4 mb-3">
                          <h3><span style={{backgroundColor:"#6A7172"}} class="badge rounded-pill ">{element.nom}</span></h3>
                        </CardTitle>
                        Adresse: {element.adresse}
                        <br/>
                        Ville: {element.ville}
                        <br/>
                        Type de terrain: {element.statusTerrain}
                        <br/>
                        <div className="mt-3">
                          <button onClick={this.supprimer.bind(this,element.id)} type="button" style={{backgroundColor:"#4f8f3f",float:"right",cursor:"pointer",borderRadius:"25px"}} className="btn btn-info btn-elegant mr-2" >
                            <i className="fas fa-trash mr-1"></i>
                          </button>
                          <button  onClick={this.handleIndisponibilite.bind(this,element.id)} type="button" style={{backgroundColor:"#4f8f3f",float:"right",cursor:"pointer",borderRadius:"25px"}} className="btn btn-info btn-elegant mr-2" >
                            <i className="bx bx-time-five bx-xs mr-1"></i>
                          </button>
                          <button onClick={this.handleDetail.bind(this,element.id)} type="button" style={{backgroundColor:"#4f8f3f",float:"right",cursor:"pointer",borderRadius:"25px"}} className="btn btn-info btn-elegant mr-2" >
                            <i className="fas fa-info-circle mr-1"></i>
                          </button>
                          <button onClick={this.handleModifier.bind(this,element.id)} type="button" style={{backgroundColor:"#4f8f3f",float:"right",cursor:"pointer",borderRadius:"25px"}} className="btn btn-info btn-elegant mr-2" >
                            <i className="fas fa-pencil-alt mr-1"></i>
                          </button>
                          
                        </div>
                      </CardBody>
                      </Card>
                      </Col>
                  ))
                 }
                    </Row>

                  </CardBody>

                </Card>
              </Col>
            </Row>
              
               
              ):(<>{this.state.page}</>)
            }
          </Container>
        </div>
        
      </React.Fragment>
    )
  }
}
export default ListeTerrain